.container {
  display: flex;
}

.image {
  margin-top: 10px;
  margin-left: 20px;
  margin-right: 30px;
  width: 300px;
  height: auto;
}

.box {
  text-align: start;
}

.title {
  font-size: 30px;
}

.rating {
  margin: 0;
  display: inline-block;
  padding: 15px;
  color: rgb(32, 62, 196);
  font-size: 20px;
  font-weight: 600;
}

.subtitle {
  font-size: 20px;
  color: rgb(0, 10, 56);
}

.descr {
  margin: 0;
  padding: 15px;
  font-weight: 400;
}

.genres {
  margin: 0;
  display: inline-block;
  padding: 5px 20px;
  font-weight: 400;
  list-style: none;
  color: rgb(32, 62, 196);
}

.link {
  display: inline-block;
  text-decoration: none;
  padding: 12px;
  font-weight: 500;
  color: #2a363b;
  font-size: 22px;
  font-weight: 700;
}

.activeLink {
  color: #2196f3;
}
.button {
  cursor: pointer;
  color: aliceblue;
  background-color: #2196f3;
  border-radius: 15%;
  padding: 5px;
  border-style: hidden;
}
